import {
  	qiniuUpload
} from '../../util/config.js'
export default {
  data() {
    return {
      msgInfo: '',
      token: '',
      qiniuaddr: '',
      imgList: []
    }
  },

  mounted() {
    this.$http.getUpToken({}).then(res => {
      // console.log(JSON.parse(res.data))
      this.token = JSON.parse(res.data).uptoken
      this.qiniuaddr = JSON.parse(res.data).url
    })
  },
  methods: {
    handleChooseImg() {
      const self = this
      uni.chooseImage({
        count: 9, // 默认9
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        // sourceType: ['album'], // 从相册选择
        success: function(res) {
          
          // if ((self.imgList.length + res.tempFilePaths.length) > 9) {
          //   uni.showToast({
          //     title: '已超过最大上传图片数量',
          //     icon: 'none',
          //     duration: 2000
          //   })
          //   return
          // }
          for (let i = 0; i < res.tempFilePaths.length; i++) {
            self.uploadQiniu(res.tempFilePaths[i],i)
          }
          
        }
      })
    },

    /**
      * 图片上传七牛云
      */
    uploadQiniu(tempFilePaths,i) {
      var self = this
      
      const keyname = Math.floor(Math.random() * 100) + new Date().getTime()
      uni.uploadFile({
        url: qiniuUpload.url,
        name: 'file',
        filePath: tempFilePaths,
        header: {
          'Content-Type': 'multipart/form-data'
        },
        formData: {
          token: this.token,
          key: 'bus/goods/' + keyname
        },
        success: function(res) {
          const data = JSON.parse(res.data)

          const imgUrl = self.qiniuaddr + '/' + data.key

          self.$emit('handleSendImgMsg', imgUrl)

          // console.log(self.imgList)
          // uni.hideLoading()
        },
        fail: function(res) {
          console.log(res)
          uni.showToast({
            title: res.errMsg,
            icon: 'none',
            duration: 2000
          })
        }
      })
    },

    sendMsg() {
      // console.log(this.msgInfo)

      if (this.msgInfo !== '' || this.msgInfo !== [] || this.msgInfo !== null) {
        this.$emit('handleSendMsg', this.msgInfo)
        this.msgInfo = ''
      }
    },

    sendImgMessage(imgUrl) {

    }

  }
}
